

.loadingScreen {

	position: fixed;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	z-index: 9999999999999999;
	background-color: rgba(95, 162, 161, 1);;

}

nav {

	text-align: center;
	padding: 1rem;
	background-color: rgba(95, 162, 161, 1);;

}

nav > a {

	padding: 1rem;
	color: white;
	-webkit-transition-property: background-color, color;
	-moz-transition-property: background-color, color;
	-o-transition-property: background-color, color;
	-webkit-transition-duration: 1s;
	-moz-transition-duration: 1s;
	-o-transition-duration: 1s;
	text-decoration: none

}
 
nav > a:hover {

	padding: 1rem;
	background-color: white;
	color: rgba(95, 162, 161, 1);;

}

.active-link {

	color: rgba(95, 162, 161, 1);;	
	background-color: white;

}

.github:hover {

	opacity: 0.5;

}


.github {

	color: white;
	font-size: 0.9rem;
	transition-property: opacity;
	transition-duration: 1s;
	margin-top: 0.3rem;
	

}

.app {

	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	
}

.imageTransition {

     	transition: all 1s ease; 

}

.scrollTest {

	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: auto;
	margin-bottom: 10rem;

}

.scrollTest > img {

	width: 100%;
	height: auto;

}

.scrollableContainer {

	height: 200px;
	overflow: scroll;
	position: relative;

}


.scrollTest > div > img {

	width: 100%;
	height: auto;

}

.scrollTest > div > div {

	height: 500px;

}

.red-line-test {

	 position: sticky;
         top: 50px;
         left: 0px;
         right: 0px;
         height: 5px !important;
         background-color: red;

}

.rosinante-presentation {

	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;


}

.rosinante-name {

	display: flex;
	justify-content: center;
	flex-direction: column;
	padding: 1rem;
	color: white;
	background-color: rgba(95, 162, 161, 1);;
	text-align: center;
	word-break: break-word;

}

.rosinante-name > div {

	font-size: 2rem;

}

.rosinante-form-explaination {

	display: grid;
	grid-template-columns: 1fr;
	margin-bottom: 5rem;
	

}

.rosinante-form {

	display: flex;
	flex-direction: column;
	width: 200px;
	padding: 1rem;
	margin-bottom: 5rem;
	margin: auto;
	color: rgba(95, 162, 161, 1);

}

.rosinante-form > label {

	padding: 0.2rem;
	padding-left: 0rem;
	font-size: 0.8rem;
	margin-bottom: 0.3rem;

}

.rosinante-form > input {

	border-radius: 5px;
	border-width: 1px;
	padding: 0.2rem;
	margin-bottom: 0.5rem;

}

.rosinante-docs-container {

	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;

}

.rosinante-docs {

	width: 60%;
	margin: auto;
	padding: 2rem;
	color: rgba(95, 162, 161, 1);

}

.rosinante-docs > p {

	line-height: 1.5rem;

}

@media only screen and (max-width: 800px) {
  
	.rosinante-docs {

		width: 80%;

	}

}

@media only screen and (max-width: 550px) {
  
	.scrollTest {

		grid-template-columns: 1fr;

	}

}

